import React,{useState} from 'react'
import api from '../../api'

import './style.css'

export default function PCUpload(){
  const [filename, setFilename] = useState(null)
  const [formData, setFormData] = useState(null)

  function changeFile(e){
    let file = e.target.files[0]
    setFilename(file.name)
    let formData = new FormData()
    formData.append('fileBlob', file)
    formData.append('filename', file.name)
    setFormData(formData)
  }
  async function upload(){
    let res = await api.post('/pc-upload', formData)
    if(res.code === 0){
      alert('')
    }
  }
  function cancel(){
    setFilename(null)
    setFormData(null)
  }
  return (
    <div id="upload-body">
      <h1 id='page-header'><span style={{color:'rgb(97, 209, 212)'}}>()</span></h1>
      <input type='file' onChange={changeFile} id='input-file' />
      {
        filename ?
        <div id='success-read-file'>
          <div>
            <span>{filename}</span>
          </div>
          <div id='buttons'>
            <div onClick={upload} id="upload-button"></div>
            <div onClick={cancel} id="cancel-button"></div>
          </div>
        </div>
        :
        <label for='input-file' id='label'>
          <div></div>
      </label>
      }
    </div>
  )
}